	var loginRes,_self,page=1;
export default {
	data() {
		return {
			myFace:'',
			user:{},
			arts:[],
			loadMore:'点击加载更多'
		}
	},
	methods: {
		//注销
		logoff(){
			uni.removeStorageSync('SUID');
						uni.removeStorageSync('SRAND');
						uni.showToast({
							title:"您已经退出悦读",
							icon: "none"
						});
						setTimeout(function(){
							uni.switchTab({
								url:'../index/index'
							});
						}, 1000);
		},
		//编辑
		editArt(e){
			var artId=e.currentTarget.dataset.artid
			uni.navigateTo({
				url:'../editArt/editArt?artId='+artId
			})
		},
		//删除
		removeArt(e){
			var artId=e.currentTarget.dataset.artid
			var index=e.currentTarget.dataset.index
			uni.showModal({
				title:'提示',
				content:'确定要删除吗？',
				success:function(e){
					if(e.confirm == true){
						uni.request({
							url: _self.apiServer+'my&m=removeArt',
							method: 'POST',
							header: {'content-type' : "application/x-www-form-urlencoded"},
							data:{
								uid: loginRes[0],
								random: loginRes[1],
								artId: artId
							},
							success: res => {
								if(res.data.status=='ok'){
									uni.showToast({title:'已删除',icon:'none'})
									_self.arts.splice(index,1)
								}else{
									uni.showToast({title:'删除失败',icon:'none'})
								}
							}
						})
					}
				}
			})
		},
		//获取文章信息
		getArtList(){
			if(this.loadMore!='点击加载更多'){return}
			this.loadMore='加载中'
			uni.request({
				url:this.apiServer+'my&m=arts&page='+page,
				method:'POST',
				header:{'content-type' : "application/x-www-form-urlencoded"},
				data:{
					uid:loginRes[0],
					random:loginRes[1]
				},
				success: res => {
					if(res.data.status=='ok'){
						this.arts=this.arts.concat(res.data.data)
						page++
						this.loadMore='点击加载更多'
					}else if(res.data.status=='empty'){
						this.loadMore='已经加载全部'
					}else{
						this.loadMore='点击加载更多'
					}
				}
			})
		}
	},
	onLoad() {
		_self=this
		loginRes=this.checkLogin('../my/my',2)
		if(!loginRes) return false
		this.myFace=loginRes[3]
	},
	onShow(){
		//加载会员信息
		uni.request({
			url:_self.apiServer+'my&m=info',
			method:'POST',
			header:{'content-type' : "application/x-www-form-urlencoded"},
			data:{
				uid:loginRes[0],
				random:loginRes[1]
			},
			success:res=>{
				// console.log(JSON.stringify(res))
				if(res.data.status=='ok'){
					this.user=res.data.data
				}else{
					console.log('cuo')
				}
			}
		})
		//初始化
		this.arts=[]
		this.loadMore='点击加载更多'
		page=1
		this.getArtList()
	}
}
